.homepage-about-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: cover;

  .homepage-about-text{
    width: 40%;
    height: 60%;
    background: rgba(71, 86, 47, 0.5);
    padding: 30px 90px 10px 50px;
    margin-top: 70px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    border-radius: 10px;

    h1 {
      color: #f5f5f5;
      font-size: 66px;
      margin: 0;
      padding-bottom: 20px;
    }

    p {
      font-size: 20px;
      line-height: 28px;
    }

    .about-span {
      font-weight: 900;
      color: black;
    }

    button {
      margin-top: 20px;
      margin-bottom: 20px;
      padding: 15px 25px;
      text-decoration: none;
      font-family: 'Cormorant', serif;
      font-size: 16px;
      border: none;
      border-radius: 10px;
      box-shadow: 0 10px 20px -8px rgba(0, 0, 0,.7);
    }

    button{
      cursor: pointer;
      display: inline-block;
      position: relative;
      transition: 0.5s;
    }

    button:after {
      content: '»';
      position: absolute;
      opacity: 0;
      top: 15px;
      right: -10px;
      transition: 0.5s;
    }

    button:hover{
      padding-right: 25px;
      padding-left: 15px;
    }

    button:hover:after {
      opacity: 1;
      right: 10px;
    }
  }

  img {
    width: 270px;
    margin-left: -40px;
    border-radius: 7px;
    box-shadow: 0 10px 20px -8px rgba(0, 0, 0,.7);
  }

   // all rules outside of @media queries apply to devices 1201px and above
   @media (max-width: 1200px) {
    //from 993px to 1200px (inclusive)
    }
    @media (max-width: 992px) {
    //from 769px to 992px (inclusive)
    }
    @media (max-width: 768px) {
    // from 0px to 768px (inclusive)
      img {
        display: none;
      }
    }


}
